: $h2-font-size; }\n.font-size-h3 { font-size: $h3-font-size; }\n.font-size-h4 { font-size: $h4-font-size; }\n.font-size-h5 { font-size: $h5-font-size; }\n.font-size-h6 { font-size: $h6-font-size; }\n\n@include media-breakpoint-down(sm) {\n    .display-1 { font-size: $display1-size / 1.5; }\n    .display-2 { font-size: $display2-size / 1.5; }\n    .display-3 { font-size: $display3-size / 1.5; }\n    .display-4 { font-size: $display4-size / 1.5; }\n\n    h1, .h1, .font-size-h1 { font-size: $h2-font-size; }\n    h2, .h2, .font-size-h2 { font-size: $h3-font-size; }\n    h3, .h3, .font-size-h3 { font-size: $h4-font-size; }\n    h4, .h4, .font-size-h4 { font-size: $h5-font-size; }\n\n    h5, .h5, .font-size-h5,\n    h6, .h6, .font-size-h6 {\n        font-size: $h6-font-size;\n    }\n}\n\n.content-heading {\n    margin-bottom: $space-mobile;\n    padding-top: 1rem;\n    padding-bottom: .5rem;\n    font-size: $font-size-lg;\n    font-weight: 400;\n    line-height: 1.75;\n    border-bottom: 1px solid $body-bg-dark;\n\n    small {\n        margin-top: .25rem;\n        font-size: 1rem;\n        font-weight: 400;\n        color: gray(\"600\");\n    }\n\n    @include media-breakpoint-up(md) {\n        margin-bottom: $space-base;\n        padding-top: 1.5rem;\n\n        small {\n            margin-top: 0;\n        }\n    }\n\n    .block-content > &:first-child,\n    .content > &:first-child {\n        padding-top: 0 !important;\n    }\n\n    .dropdown {\n        line-height: $line-height-base;\n    }\n}\n\n// Emphasis\nsmall,\n.small {\n    font-weight: inherit;\n}\n\n// Transformations\n.text-uppercase {\n    letter-spacing: .0625rem;\n}\n","//\n// Grid\n// --------------------------------------------------\n\n// Tiny grid gutters\n.row.gutters-tiny {\n    margin-right: (-($space-row-gutter-tiny / 2));\n    margin-left: (-($space-row-gutter-tiny / 2));\n\n    > .col,\n    > [class*=\"col-\"] {\n        padding-left: ($space-row-gutter-tiny / 2);\n        padding-right: ($space-row-gutter-tiny / 2);\n    }\n\n    .push,\n    .block,\n    &.items-push > div {\n        margin-bottom: $space-row-gutter-tiny;\n    }\n}\n\n// Responsive grid gutters\n@include media-breakpoint-down(sm) {\n    .row:not(.gutters-tiny):not(.no-gutters) {\n        margin-right: (-($space-mobile / 2));\n        margin-left: (-($space-mobile / 2));\n\n        > .col,\n        > [class*=\"col-\"] {\n            padding-left: ($space-mobile / 2);\n            padding-right: ($space-mobile / 2);\n        }\n    }\n}\n\n// Equal height of elements inside columns\n.row.row-deck > div {\n    display: flex;\n    align-items: stretch;\n\n    > .block {\n        min-width: 100%;\n    }\n}\n","//\n// Tables\n// --------------------------------------------------\n\n.table-vcenter {\n    th,\n    td {\n        vertical-align: middle;\n    }\n}\n\n// Table tools\n.js-table-checkable tbody tr,\n.js-table-sections-header > tr {\n    cursor: pointer;\n}\n\n.js-table-sections-header {\n    > tr > td:first-child > i {\n        transition: transform .15s ease-out;\n    }\n\n    + tbody {\n        display: none;\n    }\n\n    &.show {\n        > tr > td:first-child > i {\n            transform: rotate(90deg);\n        }\n\n        + tbody {\n            display: table-row-group;\n        }\n    }\n}\n","//\n// Forms\n// --------------------------------------------------\n\n.form-control {\n    font-family: $font-family-base;\n\n    // Alternative Style\n    &.form-control-alt {\n        border-color: $body-bg;\n        background-color: $body-bg;\n        transition: none;\n\n        &:focus {\n            border-color: $body-bg-dark;\n            background-color: $body-bg-dark;\n            box-shadow: none;\n        }\n\n        &.is-valid {\n            border-color: lighten(theme-color(\"success-light\"), 15%);\n            background-color: lighten(theme-color(\"success-light\"), 15%);\n\n            &:focus {\n                border-color: lighten(theme-color(\"success-light\"), 10%);\n                background-color: lighten(theme-color(\"success-light\"), 10%);\n            }\n        }\n\n        &.is-invalid {\n            border-color: lighten(theme-color(\"danger-light\"), 23%);\n            background-color: lighten(theme-color(\"danger-light\"), 23%);\n\n\n            &:focus {\n                border-color: lighten(theme-color(\"danger-light\"), 20%);\n                background-color: lighten(theme-color(\"danger-light\"), 20%);\n            }\n        }\n    }\n}\n","//\n// Buttons\n// --------------------------------------------------\n\n.btn {\n    font-family: $font-family-base;\n\n    &.btn-square { border-radius: 0; }\n    &.btn-rounded { border-radius: 2rem; }\n\n    .si,\n    .fa {\n        position: relative;\n        top: 1px;\n    }\n\n    &.btn-sm .fa { top: 0; }\n}\n\n// Alternate variant buttons\n.btn-alt-primary {\n    @include button-alt-variant($btn-alt-primary-bg, theme-color(\"primary-dark\"));\n}\n\n.btn-alt-secondary {\n    @include button-alt-variant($body-bg, $body-color);\n}\n\n.btn-alt-success {\n    @include button-alt-variant(lighten(theme-color(\"success-light\"), 10%), darken(theme-color(\"success\"), 25%));\n}\n\n.btn-alt-info {\n    @include button-alt-variant(lighten(theme-color(\"info-light\"), 10%), darken(theme-color(\"info\"), 25%));\n}\n\n.btn-alt-warning {\n    @include button-alt-variant(lighten(theme-color(\"warning-light\"), 10%), darken(theme-color(\"warning\"), 25%));\n}\n\n.btn-alt-danger {\n    @include button-alt-variant(lighten(theme-color(\"danger-light\"), 10%), darken(theme-color(\"danger\"), 25%));\n}\n\n.btn-alt-dark {\n    @include button-alt-variant(lighten(theme-color(\"dark\"), 60%), theme-color(\"dark\"));\n}\n\n.btn-alt-light {\n    @include button-alt-variant(theme-color(\"light\"), theme-color(\"dark\"));\n}\n\n// Hero variant buttons\n.btn-hero-primary {\n    @include button-hero-variant(theme-color(\"primary\"));\n}\n\n.btn-hero-secondary {\n    @include button-hero-variant(theme-color(\"secondary\"));\n}\n\n.btn-hero-success {\n    @include button-hero-variant(theme-color(\"success\"));\n}\n\n.btn-hero-info {\n    @include button-hero-variant(theme-color(\"info\"));\n}\n\n.btn-hero-warning {\n    @include button-hero-variant(theme-color(\"warning\"));\n}\n\n.btn-hero-danger {\n    @include button-hero-variant(theme-color(\"danger\"));\n}\n\n.btn-hero-dark {\n    @include button-hero-variant(theme-color(\"dark\"));\n}\n\n.btn-hero-light {\n    @include button-hero-variant(theme-color(\"light\"));\n}\n\n// Hero size variant buttons\n.btn-hero-lg {\n  @include button-size($btn-hero-padding-y-lg, $btn-hero-padding-x-lg, $font-size-sm, $btn-line-height, $btn-hero-border-radius);\n}\n\n.btn-hero-sm {\n  @include button-size($btn-hero-padding-y-sm, $btn-hero-padding-x-sm, $font-size-sm, $btn-line-height, $btn-hero-border-radius);\n}\n\n// Used for buttons that adapt to light/dark header and sidebar variations\n.btn-dual {\n    @include button-dual-variant(darken($body-color, 22%), darken($body-bg, 10%), $body-bg-light);\n}\n\n.page-header-dark #page-header .btn-dual,\n.sidebar-dark #sidebar .btn-dual {\n    @include button-dual-variant($white, darken($header-dark-bg, 6%), lighten($header-dark-bg, 4.5%));\n}\n\n.page-header-dark.page-header-glass:not(.page-header-scroll) #page-header .btn-dual {\n    @include button-dual-variant($white, darken($header-dark-bg, 6%));\n}\n","//\n// Buttons\n// --------------------------------------------------\n\n// Hero variant\n@mixin button-hero-variant($background, $hover-background: lighten($background, 10%), $active-background: darken($background, 15%)) {\n    $btn-hero-box-shadow: 0 .125rem .75rem rgba($active-background, .25);\n    $btn-hero-box-shadow-hover: 0 .375rem .75rem rgba($active-background, .4);\n\n    color: color-yiq($background);\n    text-transform: uppercase;\n    letter-spacing: .0625rem;\n    font-weight: 700;\n    @include button-size($btn-hero-padding-y, $btn-hero-padding-x, $font-size-sm, $btn-line-height, $btn-hero-border-radius);\n    @include gradient-bg($background);\n    border: none;\n    box-shadow: $btn-hero-box-shadow;\n    transition: $btn-transition, transform .12s ease-out;\n\n    @include hover() {\n        color: color-yiq($hover-background);\n        @include gradient-bg($hover-background);\n        box-shadow: $btn-hero-box-shadow-hover;\n        transform: translateY(-1px);\n    }\n\n    &:focus,\n    &.focus {\n        color: color-yiq($hover-background);\n        @include gradient-bg($hover-background);\n        box-shadow: $btn-hero-box-shadow;\n    }\n\n    // Disabled comes first so active can properly restyle\n    &.disabled,\n    &:disabled {\n        color: color-yiq($background);\n        background-color: $background;\n        box-shadow: $btn-hero-box-shadow;\n        transform: translateY(0);\n    }\n\n    &:not(:disabled):not(.disabled):active,\n    &:not(:disabled):not(.disabled).active,\n    .show > &.dropdown-toggle {\n        color: color-yiq($active-background);\n        background-color: $active-background;\n\n        @if $enable-gradients {\n            background-image: none; // Remove the gradient for the pressed/active state\n        }\n\n        box-shadow: $btn-hero-box-shadow;\n        transform: translateY(0);\n\n        &:focus {\n            box-shadow: $btn-hero-box-shadow;\n        }\n    }\n}\n\n// Alternate variant\n@mixin button-alt-variant($background, $text-color,\n                            $hover-background: darken($background, 7.5%), $hover-text-color: $text-color,\n                            $active-background: darken($background, 12%), $active-text-color: darken($text-color, 15%)) {\n    $btn-alt-box-shadow: 0 0 0 0.2rem rgba($active-background, .25);\n    \n    color: $text-color;\n    @include gradient-bg($background);\n    border-color: $background;\n\n    @include hover() {\n        color: $hover-text-color;\n        @include gradient-bg($hover-background);\n        border-color: $hover-background;\n    }\n\n    &:focus,\n    &.focus {\n        color: $hover-text-color;\n        @include gradient-bg($hover-background);\n        border-color: $hover-background;\n        box-shadow: $btn-alt-box-shadow;\n    }\n\n    // Disabled comes first so active can properly restyle\n    &.disabled,\n    &:disabled {\n        color: color-yiq($background);\n        background-color: $background;\n        border-color: $background;\n\n        // Remove CSS gradients if they're enabled\n        @if $enable-gradients {\n            background-image: none;\n        }\n    }\n\n    &:not(:disabled):not(.disabled):active,\n    &:not(:disabled):not(.disabled).active,\n    .show > &.dropdown-toggle {\n        color: $active-text-color;\n        background-color: $active-background;\n        border-color: $active-background;\n\n        @if $enable-gradients {\n            background-image: none; // Remove the gradient for the pressed/active state\n        }\n\n        &:focus {\n            box-shadow: $btn-alt-box-shadow;\n        }\n    }\n}\n\n// Used for buttons that adapt to light/dark header and sidebar variations\n@mixin button-dual-variant($color, $background-hover, $background: transparent) {\n    color: $color;\n    background-color: $background;\n    border-color: $background;\n\n    &:hover,\n    &:focus,\n    &.focus {\n        color: $color;\n        background-color: $background-hover;\n        border-color: $background-hover;\n        box-shadow: none;\n    }\n\n    &.disabled,\n    &:disabled {\n        background-color: transparent;\n        border-color: transparent;\n    }\n\n    &:active,\n    &.active {\n        color: $color;\n        background-color: $background;\n        border-color: $background;\n    }\n\n    &:not(:disabled):not(.disabled):active,\n    &:not(:disabled):not(.disabled).active,\n    .show>&.dropdown-toggle {\n        color: $color;\n        background-color: $background-hover;\n        border-color: $background-hover;\n    }\n}","//\n// Transitions\n// --------------------------------------------------\n\n.fade {\n    transition: opacity .15s ease-out;\n\n    &.fade-up,\n    &.fade-right,\n    &.fade-left {\n        transition: opacity .15s ease-out, transform .15s ease-out;\n    }\n\n    &.fade-up {\n        transform: translateY(50px);\n\n        &.show {\n            transform: translateY(0);\n        }\n    }\n\n    &.fade-right {\n        transform: translateX(-50px);\n\n        &.show {\n            transform: translateX(0);\n        }\n    }\n\n    &.fade-left {\n        transform: translateX(50px);\n\n        &.show {\n            transform: translateX(0);\n        }\n    }\n}\n","//\n// Dropdown\n// --------------------------------------------------\n\n.dropdown-menu {\n    padding-right: .5rem;\n    padding-left: .5rem;\n    box-shadow: $dropdown-box-shadow;\n\n    &.dropdown-menu-lg {\n        min-width: 300px;\n    }\n}\n\n.dropdown-item {\n    margin-bottom: .25rem;\n    border-radius: $border-radius;\n}","//\n// Input Group\n// --------------------------------------------------\n\n.input-group-text.input-group-text-alt {\n    background-color: $body-bg-dark;\n    border-color: $body-bg-dark;\n}\n","//\n// Custom Forms\n// --------------------------------------------------\n\n// Switch controls\n.custom-switch {\n    padding-left: 0;\n\n    .custom-control-label {\n        left: 0;\n        padding-left: 2.5rem;\n\n        &::before {\n            top: (($line-height-base - 1.25rem) / 2);\n            left: 0;\n            width: 2rem;\n            height: 1.25rem;\n            border-radius: 1rem;\n            transition: background-color .15s ease-out;\n        }\n\n        &::after {\n            top: (($line-height-base - 1rem) / 2);\n            left: .125rem;\n            width: 1rem;\n            height: 1rem;\n            transform: none;\n            background-color: $white;\n            border-radius: 1rem;\n            box-shadow: .125rem 0 .125rem rgba(0,0,0,.1);\n        }\n    }\n\n    .custom-control-input:checked ~ .custom-control-label::after {\n        transform: translateX(.75rem);\n        background-color: $white;\n        box-shadow: -.125rem 0 .125rem rgba(0,0,0,.2);\n    }\n}\n\n// Block controls\n.custom-block {\n    padding: 0;\n\n    .custom-control-label {\n        width: 100%;\n        padding: .75rem;\n        background-color: $white ;\n        border: 2px solid darken(theme-color('light'), 10%);\n        border-radius: $border-radius-sm;\n        cursor: pointer;\n\n        &:hover {\n            border-color: darken(theme-color('light'), 20%);\n        }\n\n        &::before,\n        &::after {\n            display: none;\n        }\n    }\n\n    .custom-block-indicator {\n        position: absolute;\n        display: none;\n        top: -.5rem;\n        right: -.5rem;\n        width: 1.75rem;\n        height: 1.75rem;\n        line-height: 1.75rem;\n        color: $white;\n        font-size: .625rem;\n        text-align: center;\n        background-color: darken(theme-color('light'), 30%);\n        border-radius: .875rem;\n        z-index: 1;\n    }\n\n    .custom-control-input:checked ~ .custom-control-label {\n        border-color: darken(theme-color('light'), 30%);\n    }\n\n    .custom-control-input:focus ~ .custom-control-label {\n        box-shadow: 0 0 .5rem rgba(0,0,0,.15);\n    }\n\n    .custom-control-input:checked ~ .custom-block-indicator {\n        display: block;\n    }\n\n    .custom-control-input:disabled + label,\n    .custom-control-input[readonly] + label,\n    .custom-control-input:disabled + label + span,\n    .custom-control-input[readonly] + label + span {\n        opacity: .5;\n    }\n}\n\n// Add transitions to custom controls\n.custom-control-label::before {\n    border: none;\n}\n\n.custom-control-label::after {\n    transition: transform .15s ease-out;\n}\n\n// Checkboxes shape variants and icon rotate transforms\n.custom-checkbox {\n    &.custom-checkbox-square {\n        .custom-control-label::before {\n            border-radius: 0;\n        }\n    }\n\n    &.custom-checkbox-rounded-circle {\n        .custom-control-label::before {\n            border-radius: 1rem;\n        }\n    }\n\n    .custom-control-label::after {\n        transform: rotate(-90deg);\n    }\n\n    .custom-control-input:checked ~ .custom-control-label::after {\n        transform: rotate(0deg);\n    }\n}\n\n// Color variants for all custom controls\n.custom-control-primary {\n    @include custom-control-variant($body-bg-dark, theme-color(\"primary\"));\n}\n\n.custom-control-success {\n    @include custom-control-variant(lighten(theme-color(\"success-light\"), 10%), theme-color(\"success\"));\n}\n\n.custom-control-info {\n    @include custom-control-variant(lighten(theme-color(\"info-light\"), 10%), theme-color(\"info\"));\n}\n\n.custom-control-warning {\n    @include custom-control-variant(lighten(theme-color(\"warning-light\"), 10%), theme-color(\"warning\"));\n}\n\n.custom-control-danger {\n    @include custom-control-variant(lighten(theme-color(\"danger-light\"), 10%), theme-color(\"danger\"));\n}\n\n.custom-control-light {\n    @include custom-control-variant(gray(\"300\"), gray(\"500\"));\n}\n\n.custom-control-dark {\n    @include custom-control-variant(gray(\"300\"), theme-color(\"dark\"));\n}\n\n// Size variants for all custom controls\n.custom-control-lg {\n    padding-left: 1.75rem;\n\n    .custom-control-label {\n        &::before,\n        &::after {\n            top: (($line-height-base - 1.25rem) / 2);\n            left: -1.75rem;\n            width: 1.25rem;\n            height: 1.25rem;\n        }\n    }\n\n    &.custom-switch {\n        padding-left: 0;\n\n        .custom-control-label {\n            padding-left: 3.25rem;\n\n            &::before {\n                top: (($line-height-base - 1.625rem) / 2);\n                left: 0;\n                width: 2.75rem;\n                height: 1.625rem;\n            }\n\n            &::after {\n                top: (($line-height-base - 1.25rem) / 2);\n                left: .25rem;\n                width: 1.25rem;\n                height: 1.25rem;\n            }\n        }\n\n        .custom-control-input:checked ~ .custom-control-label::after {\n            transform: translateX(1rem);\n        }\n    }\n}\n","//\n// Custom Forms\n// --------------------------------------------------\n\n// Custom control variant\n@mixin custom-control-variant($background, $background-checked) {\n    .custom-control-input {\n\n        &:checked ~ .custom-control-label::before {\n            background-color: $background-checked;\n        }\n\n        &:focus ~ .custom-control-label::before {\n            box-shadow: 0 0 0 1px #fff, 0 0 0 .2rem rgba($background-checked, .25);\n        }\n\n        &:not(:disabled):active~.custom-control-label::before {\n            background-color: lighten($background-checked, 20%);\n        }\n    }\n\n    .custom-control-label::before {\n        background-color: $background;\n    }\n\n    &.custom-checkbox {\n        .custom-control-input:checked ~ .custom-control-label::before {\n            background-color: $background-checked;\n        }\n\n        .custom-control-input:indeterminate ~ .custom-control-label::before {\n            background-color: $background-checked;\n        }\n    }\n\n    &.custom-radio {\n        .custom-control-input:checked ~ .custom-control-label::before {\n            background-color: $background-checked;\n        }\n    }\n\n    &.custom-block {\n        .custom-block-indicator {\n            background-color: $background-checked;\n        }\n\n        .custom-control-input:checked ~ .custom-control-label {\n            border-color: $background-checked;\n        }\n\n        .custom-control-input:focus ~ .custom-control-label {\n            box-shadow: 0 0 .75rem rgba($background-checked,.25);\n        }\n    }\n}\n","//\n// Nav\n// --------------------------------------------------\n\n// Links\n.nav-link {\n    font-weight: $font-weight-bold;\n}\n\n// Nav Pills\n.nav-pills {\n    .nav-link {\n        @include hover-focus {\n            background-color: $body-bg;\n        }\n    }\n\n    .nav-link.active,\n    .show > .nav-link {\n        color: $nav-pills-link-active-color;\n        background-color: $nav-pills-link-active-bg;\n    }\n}\n\n// Tabs block variation\n.nav-tabs-block {\n    background-color: $body-bg-light;\n    border-bottom: none;\n\n    .nav-item {\n        margin-bottom: 0;\n    }\n\n    .nav-link {\n        padding-top: .75rem;\n        padding-bottom: .75rem;\n        border-color: transparent;\n        border-radius: 0;\n\n        @include hover-focus {\n            color: theme-color(\"primary\");\n            background-color: $body-bg;\n            border-color: transparent;\n        }\n    }\n\n    .nav-link.active,\n    .nav-item.show .nav-link {\n        color: $body-color;\n        background-color: $white;\n        border-color: transparent;\n    }\n}\n\n// Tabs block alternative variation\n.nav-tabs-alt {\n    background-color: transparent;\n    border-bottom-color: $body-bg-dark;\n\n    .nav-item {\n        margin-bottom: -2px;\n    }\n\n    .nav-link {\n        padding-top: .75rem;\n        padding-bottom: .75rem;\n        background-color: transparent;\n        border-color: transparent;\n\n        @include hover-focus {\n            color: theme-color(\"primary\");\n            background-color: transparent;\n            border-color: transparent;\n            box-shadow: inset 0 -3px theme-color(\"primary\");\n        }\n    }\n\n    .nav-link.active,\n    .nav-item.show .nav-link {\n        color: $body-color;\n        background-color: transparent;\n        border-color: transparent;\n        box-shadow: inset 0 -3px theme-color(\"primary\");\n    }\n}\n\n// Various Items Navigation\n.nav-items {\n    @include list-unstyled;\n\n    a {\n        border-bottom: 1px solid $body-bg;\n\n        @include hover() {\n            background-color: $body-bg-light;\n        }\n\n        &:active {\n            background-color: $body-bg;\n        }\n    }\n\n    > li:last-child > a {\n        border-bottom: none;\n    }\n}\n","//\n// Breadcrumb\n// --------------------------------------------------\n\n.breadcrumb.breadcrumb-alt {\n    .breadcrumb-item + .breadcrumb-item::before {\n        position: relative;\n        top: 1px;\n        width: 1rem;\n        font-family: \"Font Awesome 5 Free\";\n        font-weight: 900;\n        color: gray(\"400\");\n        content: \"\\f105\";\n    }\n}\n","//\n// Pagination\n// --------------------------------------------------\n\n.page-link {\n    font-weight: $font-weight-bold;\n\n    &:focus {\n        background-color: $body-bg-dark;\n        border-color: $body-bg-dark;\n    }\n}","//\n// Badge\n// --------------------------------------------------\n\n.badge-secondary {\n    @include badge-variant(rgba($black,.33));\n}","//\n// Modal\n// --------------------------------------------------\n\n// Top position\n.modal-dialog.modal-dialog-top {\n    margin-top: 0 !important;\n}\n\n// Modal Transitions\n.modal {\n    &.fade .modal-dialog {\n        transition-duration: .4s;\n\n        &.modal-dialog-popin {\n            transform: scale(1.25, 1.25);\n        }\n\n        &.modal-dialog-popout {\n            transform: scale(.75, .75);\n        }\n\n        &.modal-dialog-slideup {\n            transform: translate(0, 30%);\n        }\n\n        &.modal-dialog-slideright {\n            transform: translate(-30%, 0);\n        }\n\n        &.modal-dialog-slideleft {\n            transform: translate(30%, 0);\n        }\n\n        &.modal-dialog-fromright {\n            transform: translateX(30%) rotate(10deg) scale(.75);\n        }\n\n        &.modal-dialog-fromleft {\n            transform: translateX(-30%) rotate(-10deg) scale(.75);\n        }\n    }\n\n    &.show .modal-dialog {\n        &.modal-dialog-popin,\n        &.modal-dialog-popout {\n            transform: scale(1, 1);\n        }\n\n        &.modal-dialog-slideup,\n        &.modal-dialog-slideright,\n        &.modal-dialog-slideleft {\n            transform: translate(0, 0);\n        }\n\n        &.modal-dialog-fromright,\n        &.modal-dialog-fromleft {\n            transform: translateX(0) rotate(0) scale(1);\n        }\n    }\n}\n","//\n// Print\n// --------------------------------------------------\n\n@media print {\n    #page-container,\n    #main-container {\n        padding: 0 !important;\n    }\n\n    #page-overlay,\n    #page-loader,\n    #page-header,\n    #page-footer,\n    #sidebar,\n    #side-overlay,\n    .block-options {\n        display: none !important;\n    }\n}\n","//\n// Layout\n// --------------------------------------------------\n\n// Main Structure\n#page-container {\n    display: flex;\n    flex-direction: column;\n    margin: 0 auto;\n    width: 100%;\n    min-width: 320px;\n    min-height: 100vh;\n}\n\n#page-overlay {\n    position: fixed;\n    top: 0;\n    right: 0;\n    bottom: 0;\n    left: 0;\n    content: \"\";\n    background-color: rgba($black, .4);\n    z-index: $zindex-page-overlay;\n    opacity: 0;\n    transition: opacity .25s ease-out;\n    transform: translateY(-100%);\n\n    @include media-breakpoint-up(lg) {\n        .side-overlay-o.enable-page-overlay & {\n            transform: translateY(0);\n            opacity: 1;\n        }\n    }\n}\n\n#main-container {\n    display: flex;\n    flex-direction: column;\n    flex: 1 0 auto;\n    max-width: 100%;\n}\n\n#page-footer {\n    display: flex;\n    align-items: center;\n    width: 100%;\n    min-height: $footer-height;\n\n    &.footer-static {\n        display: block;\n        min-height: auto;\n    }\n}\n\n// Main Content\n.content {\n    @include content-layout($space-mobile, $space-mobile);\n\n    @include media-breakpoint-up(md) {\n        @include content-layout($space-base, $space-base);\n    }\n\n    @include media-breakpoint-up(xl) {\n        &.content-narrow { width: $space-narrow !important; }\n    }\n\n    &.content-boxed { max-width: $space-boxed !important; }\n\n    &.content-top {\n        padding-top: $header-height + $space-mobile;\n\n        @include media-breakpoint-up(lg) {\n            padding-top: $header-height + $space-base;\n        }\n    }\n}\n\n.content-section-top {\n    padding-top: $header-height;\n}\n\n.content-max-width {\n    max-width: $space-main-max-width;\n}\n\n// Side Content\n.content-side {\n    @include content-layout($space-side, $space-side, hidden);\n}\n","//\n// Content\n// --------------------------------------------------\n\n@mixin content-layout($x, $y, $overflow: visible) {\n    width: 100%;\n    margin: 0 auto;\n    padding: $y $x 1px;\n    overflow-x: $overflow;\n\n    > .pull-t,\n    > .pull-y {\n        margin-top: -$y;\n    }\n\n    > .pull-b,\n    > .pull-y {\n        margin-bottom: -1px;\n    }\n\n    > .pull-r,\n    > .pull-x {\n        margin-right: -$x;\n    }\n\n    > .pull-r,\n    > .pull-x {\n        margin-left: -$x;\n    }\n\n    > .pull {\n        margin: (-$y) (-$x) (-1px);\n    }\n\n    @at-root {\n        #{&}#{&}-full {\n            padding-bottom: $y;\n\n            > .pull-b,\n            > .pull-y,\n            > .pull {\n                margin-bottom: -$y;\n            }\n        }\n    }\n\n    p,\n    .push,\n    .block,\n    .items-push > div {\n        margin-bottom: $y;\n    }\n\n    .items-push-2x > div {\n        margin-bottom: $y*2;\n    }\n\n    .items-push-3x > div {\n        margin-bottom: $y*3;\n    }\n}","//\n// Header\n// --------------------------------------------------\n\n#page-header {\n    position: relative;\n    margin: 0 auto;\n    width: 100%;\n    background-color: $header-bg;\n}\n\n// Header overlay (used for various reasons eg: loading or search)\n.overlay-header {\n    position: absolute;\n    top: 0;\n    right: 0;\n    bottom: 0;\n    left: 0;\n    background-color: $white;\n    opacity: 0;\n    transition: transform .2s ease-in-out, opacity .2s ease-in-out;\n    transform: translateY(-100%);\n    will-change: opacity, transform;\n\n    &.show {\n        opacity: 1;\n        transform: translateY(0);\n    }\n}\n\n// Content header sections\n.content-header {\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    margin: 0 auto;\n    height: $header-height;\n\n    @at-root #page-header & {\n        padding-left: $space-mobile;\n        padding-right: $space-mobile;\n\n        @include media-breakpoint-up(md) {\n            padding-left: $space-base;\n            padding-right: $space-base;\n        }\n    }\n\n    @at-root #sidebar &,\n    #side-overlay & {\n        padding-left: $space-side;\n        padding-right: $space-side;\n    }\n}\n","//\n// Sidebar\n// --------------------------------------------------\n\n// Mini Sidebar display helper classes\n.smini-visible,\n.smini-visible-block {\n    display: none;\n}\n\n.smini-show { opacity: 0; }\n\n.smini-show,\n.smini-hide {\n    transition: opacity $side-transition;\n}\n\n#sidebar {\n    position: fixed;\n    top: 0;\n    bottom: 0;\n    left: 0;\n    z-index: $zindex-sidebar;\n    width: 100%;\n    height: 100%;\n    overflow-y: visible;\n    background-color: $sidebar-bg;\n    transform: translateX(-100%) translateY(0) translateZ(0);\n    -webkit-overflow-scrolling: touch;\n    will-change: transform;\n\n    .side-trans-enabled & {\n        transition: transform $side-transition;\n    }\n\n    .sidebar-r & {\n        right: 0;\n        left: auto;\n        transform: translateX(100%) translateY(0) translateZ(0);\n    }\n\n    .js-sidebar-scroll {\n        height: calc(100% - #{$header-height});\n        overflow-y: auto;\n\n        &.full-height {\n            height: 100%;\n        }\n    }\n\n    @include media-breakpoint-down(md) {\n        .sidebar-o-xs & {\n            transform: translateX(0) translateY(0) translateZ(0);\n        }\n    }\n\n    @include media-breakpoint-up(lg) {\n        width: $sidebar-width;\n\n        .sidebar-o & {\n            transform: translateX(0) translateY(0) translateZ(0);\n        }\n\n        // Mini Sidebar\n        .sidebar-mini.sidebar-o & {\n            overflow-x: hidden;\n            transform: translateX(-($sidebar-width - $sidebar-mini-width)) translateY(0) translateZ(0);\n            transition: transform $side-transition;\n        }\n\n        .sidebar-mini.sidebar-o.sidebar-r & {\n            transform: translateX($sidebar-width - $sidebar-mini-width) translateY(0) translateZ(0);\n        }\n\n        .sidebar-mini.sidebar-o & .content-side,\n        .sidebar-mini.sidebar-o & .content-header {\n            width: $sidebar-width;\n            transform: translateX(($sidebar-width - $sidebar-mini-width)) translateY(0) translateZ(0);\n            transition: transform $side-transition;\n            will-change: transform;\n        }\n\n        .sidebar-mini.sidebar-o & .content-header {\n            // Small fix for correct visibility of dropdown menu when hovering over sidebar mini and it is open\n            position: relative;\n            z-index: 1;\n        }\n\n        .sidebar-mini.sidebar-o &:hover,\n        .sidebar-mini.sidebar-o &:hover .content-side,\n        .sidebar-mini.sidebar-o.sidebar-r & .content-side,\n        .sidebar-mini.sidebar-o &:hover .content-header,\n        .sidebar-mini.sidebar-o.sidebar-r & .content-header {\n            transform: translateX(0);\n        }\n\n        // Mini Mode\n        .sidebar-mini.sidebar-o & {\n            // Main navigation\n            .nav-main {\n                &,\n                .nav-main-link-icon {\n                    transition: transform $side-transition;\n                }\n\n                .nav-main-heading,\n                .nav-main-link-name,\n                .nav-main-link-badge {\n                    transition: opacity $side-transition;\n                }\n            }\n        }\n\n        // Mini Mode (styles do not apply on hover)\n        .sidebar-mini.sidebar-o &:not(:hover) {\n            // Display helper classes\n            .smini-hide { opacity: 0; }\n            .smini-show { opacity: 1; }\n\n            .smini-hidden { display: none; }\n\n            .smini-visible { display: inline-block; }\n            .smini-visible-block { display: block; }\n\n            // Main navigation\n            .nav-main {\n                transform: translateX(-$space-side);\n\n                .nav-main-link-icon {\n                    transform: translateX(.75rem);\n                }\n\n                .nav-main-heading,\n                .nav-main-link-name,\n                .nav-main-link-badge {\n                    opacity: 0;\n                }\n\n                .nav-main-link.active,\n                .nav-main-item.open > .nav-main-link-submenu {\n                    border-radius: 0;\n                }\n\n                > .nav-main-item > .nav-main-submenu { display: none; }\n            }\n        }\n    }\n}\n","//\n// Side Overlay\n// --------------------------------------------------\n\n#side-overlay {\n    position: fixed;\n    top: 0;\n    right: 0;\n    bottom: 0;\n    z-index: $zindex-side-overlay;\n    width: 100%;\n    height: 100%;\n    background-color: $side-overlay-bg;\n    overflow-y: auto;\n    transform: translateX(100%) translateY(0) translateZ(0);\n    opacity: 0;\n    -webkit-overflow-scrolling: touch;\n    will-change: transform;\n\n    .side-trans-enabled & {\n        transition: transform $side-transition, opacity $side-transition;\n    }\n\n    .sidebar-r & {\n        right: auto;\n        left: 0;\n        transform: translateX(-100%) translateY(0) translateZ(0);\n    }\n\n    .side-overlay-o & {\n        transform: translateX(0) translateY(0) translateZ(0);\n        opacity: 1;\n    }\n\n    @include media-breakpoint-up(lg) {\n        width: $side-overlay-width;\n        box-shadow: 0 0 1.5rem rgba(0,0,0,.15);\n        transform: translateX(110%) translateY(0) translateZ(0);\n\n        .sidebar-r & {\n            transform: translateX(-110%) translateY(0) translateZ(0);\n        }\n\n        .side-overlay-hover & {\n            transform: translateX($side-overlay-width - 15px) translateY(0) translateZ(0);\n            opacity: 1;\n        }\n\n        .sidebar-r.side-overlay-hover & {\n            transform: translateX(-($side-overlay-width - 15px)) translateY(0) translateZ(0);\n            opacity: 1;\n        }\n\n        .side-overlay-hover &:hover,\n        .side-overlay-o &,\n        .side-overlay-o.side-overlay-hover & {\n            box-shadow: 0 0 1.25rem rgba($black,.15);\n            transform: translateX(0) translateY(0) translateZ(0);\n            opacity: 1;\n        }\n    }\n}\n","//\n// Layout Variations\n// --------------------------------------------------\n\n#page-container {\n    // Main content\n    > #page-header .content-header,\n    > #page-header .content,\n    > #main-container .content,\n    > #page-footer .content {\n        max-width: $space-main-max-width;\n    }\n\n    @include media-breakpoint-up(xl) {\n        &.main-content-narrow {\n            > #page-header .content-header,\n            > #page-header .content,\n            > #main-container .content,\n            > #page-footer .content {\n                width: $space-narrow;\n            }\n        }\n    }\n\n    &.main-content-boxed {\n        > #page-header .content-header,\n        > #page-header .content,\n        > #main-container .content,\n        > #page-footer .content {\n            max-width: $space-boxed;\n        }\n    }\n\n    // Page header\n    &.page-header-dark #page-header {\n        color: darken($body-color-light, 8%);\n        background-color: $header-dark-bg;\n    }\n\n    &.page-header-glass {\n        #page-header {\n            position: absolute;\n            background-color: transparent;\n        }\n\n        &.page-header-fixed {\n            #page-header {\n                transition: background-color .2s linear;\n                will-change: background-color;\n                box-shadow: none;\n            }\n\n            &.page-header-scroll #page-header {\n                background-color: rgba($header-bg, .9);\n                box-shadow: 0 .25rem .625rem rgba(0,0,0,.02);\n                backdrop-filter: blur(1rem);\n            }\n\n            &.page-header-scroll.page-header-dark #page-header {\n                background-color: rgba($header-dark-bg, .9);\n                box-shadow: none;\n                backdrop-filter: blur(1rem);\n            }\n        }\n\n        #main-container,\n        &.page-header-fixed #main-container {\n            padding-top: 0;\n        }\n    }\n\n    &.page-header-fixed {\n        #page-header {\n            position: fixed;\n            box-shadow: 0 5px 10px rgba(0,0,0,.02);\n        }\n\n        &.page-header-dark #page-header {\n            box-shadow: none;\n        }\n\n        #main-container {\n            padding-top: $header-height;\n        }\n    }\n\n    &.page-header-fixed,\n    &.page-header-glass {\n        #page-header {\n            top: 0;\n            right: 0;\n            left: 0;\n            z-index: $zindex-fixed;\n            min-width: 320px;\n            max-width: 100%;\n            width: auto;\n        }\n\n        @include media-breakpoint-up(lg) {\n            &.sidebar-o #page-header {\n                padding-left: $sidebar-width;\n\n                .overlay-header {\n                    left: $sidebar-width;\n                }\n            }\n\n            &.sidebar-r.sidebar-o #page-header {\n                padding-right: $sidebar-width;\n                padding-left: 0;\n\n                .overlay-header {\n                    right: $sidebar-width;\n                    left: 0;\n                }\n            }\n\n            // Mini Sidebar\n            &.sidebar-mini.sidebar-o #page-header {\n                padding-left: $sidebar-mini-width;\n\n                .overlay-header {\n                    left: $sidebar-mini-width;\n                }\n            }\n\n            &.sidebar-mini.sidebar-r.sidebar-o #page-header {\n                padding-right: $sidebar-mini-width;\n                padding-left: 0;\n\n                .overlay-header {\n                    right: $sidebar-mini-width;\n                    left: 0;\n                }\n            }\n        }\n    }\n\n    // Page Footer\n    &.page-footer-fixed {\n        #page-footer {\n            position: fixed;\n            height: $footer-height;\n        }\n\n        #main-container {\n            padding-bottom: $footer-height;\n        }\n\n        #page-footer {\n            bottom: 0;\n            right: 0;\n            left: 0;\n            z-index: $zindex-fixed;\n            min-width: 320px;\n            max-width: 100%;\n            width: auto;\n        }\n\n        @include media-breakpoint-up(lg) {\n            &.sidebar-o #page-footer {\n                padding-left: $sidebar-width;\n            }\n\n            &.sidebar-r.sidebar-o #page-footer {\n                padding-right: $sidebar-width;\n                padding-left: 0;\n            }\n        }\n    }\n\n    // Sidebar and Side Overlay\n    &.sidebar-dark #sidebar {\n        color: $body-color-light;\n        background-color: $sidebar-dark-bg;\n        box-shadow: none;\n    }\n\n    // Side Scroll\n    &.side-scroll {\n        #sidebar .js-sidebar-scroll,\n        #side-overlay {\n            overflow-y: visible;\n        }\n\n        @include media-breakpoint-up(lg) {\n            #sidebar {\n                .content-header,\n                .content-side {\n                    width: $sidebar-width !important;\n                }\n            }\n\n            #side-overlay {\n                .content-header,\n                .content-side {\n                    width: $side-overlay-width !important;\n                }\n            }\n        }\n    }\n\n    @include media-breakpoint-up(lg) {\n        &.sidebar-o {\n            padding-left: $sidebar-width;\n        }\n\n        &.sidebar-r.sidebar-o {\n            padding-right: $sidebar-width;\n            padding-left: 0;\n        }\n\n        // Mini Sidebar\n        &.sidebar-mini.sidebar-o {\n            padding-left: $sidebar-mini-width;\n        }\n\n        &.sidebar-mini.sidebar-o.sidebar-r {\n            padding-right: $sidebar-mini-width;\n            padding-left: 0;\n        }\n    }\n}\n","//\n// Hero\n// --------------------------------------------------\n\n.hero {\n    position: relative;\n    display: flex;\n    flex-direction: row;\n    justify-content: center;\n    align-items: center;\n    width: 100%;\n    height: 100vh;\n    min-height: 500px;\n\n    &.hero-sm {\n        min-height: 300px;\n    }\n\n    &.hero-lg {\n        min-height: 800px;\n    }\n\n    &-inner {\n        flex: 0 0 auto;\n        width: 100%;\n    }\n\n    &-meta {\n        position: absolute;\n        right: 0;\n        bottom: 0;\n        left: 0;\n        padding: 1rem 0;\n        text-align: center;\n    }\n}\n\n.hero-static {\n    min-height: 100vh;\n}\n","//\n// Blocks\n// --------------------------------------------------\n\n.block {\n    margin-bottom: $space-base;\n    background-color: $white;\n    box-shadow: 0 1px 3px rgba(darken($body-bg-dark, 2.5%), .5), 0 1px 2px rgba(darken($body-bg-dark, 2.5%), .5);\n\n    & &,\n    .content-side & {\n        box-shadow: none;\n    }\n}\n\n.block-header {\n    display: flex;\n    flex-direction: row;\n    justify-content: space-between;\n    align-items: center;\n    padding: .75rem $space-block;\n    transition: opacity .25s ease-out;\n\n    &.block-header-rtl {\n        flex-direction: row-reverse;\n\n        .block-title {\n            text-align: right;\n        }\n\n        .block-options {\n            padding-right: $space-block;\n            padding-left: 0;\n        }\n    }\n\n    &-default {\n        background-color: $body-bg-light;\n    }\n}\n\n.block-title {\n    flex: 1 1 auto;\n    min-height: 1.75rem;\n    margin: 0;\n    font-size: $font-size-base * 1.125;\n    font-weight: 400;\n    line-height: 1.75;\n\n    small {\n        font-size: 1rem;\n        font-weight: 400;\n        line-height: 1.375rem;\n        color: gray(\"600\");\n    }\n}\n\n.block-content {\n    transition: opacity .25s ease-out;\n    @include content-layout($space-block, $space-block);\n\n    &.block-content-sm {\n        padding-top: ($space-block / 2);\n\n        > .pull-t,\n        > .pull-y,\n        > .pull {\n            margin-top: -($space-block / 2);\n        }\n\n        &.block-content-full {\n            padding-bottom: ($space-block / 2);\n\n            > .pull-b,\n            > .pull-y,\n            > .pull {\n                margin-bottom: -($space-block / 2);\n            }\n        }\n    }\n}\n\n// Block Variations\n.block {\n    &.block-bordered {\n        border: 1px solid $body-bg-dark;\n        box-shadow: none;\n    }\n\n    &.block-rounded {\n        border-radius: $border-radius;\n\n        > .block-header,\n        > .nav-tabs {\n            @include border-top-radius($border-radius-sm);\n\n            &:last-child {\n                @include border-bottom-radius($border-radius-sm);\n            }\n        }\n\n        &.block-mode-hidden > .block-header.block-header-default {\n            @include border-bottom-radius($border-radius-sm);\n        }\n\n        > .block-content {\n            &:first-child {\n                @include border-top-radius($border-radius-sm);\n            }\n\n            &:last-child {\n                @include border-bottom-radius($border-radius-sm);\n            }\n        }\n\n        > .nav-tabs > .nav-item {\n            &:first-child > .nav-link {\n                border-top-left-radius: $border-radius-sm;\n            }\n\n            &:last-child > .nav-link {\n                border-top-right-radius: $border-radius-sm;\n            }\n        }\n    }\n\n    &.block-themed > .block-header {\n        border-bottom: none;\n        color: $white;\n        background-color: theme-color(\"primary\");\n\n        > .block-title {\n            color: rgba($white, .9);\n\n            small {\n                color: rgba($white, .7);\n            }\n        }\n    }\n\n    &.block-transparent {\n        background-color: transparent;\n        box-shadow: none;\n    }\n}\n\n// Block Modes\n.block {\n    &.block-mode-hidden {\n        &.block-bordered > .block-header {\n            border-bottom: none;\n        }\n\n        > .block-content {\n            display: none;\n        }\n    }\n\n    &.block-mode-loading {\n        position: relative;\n        overflow: hidden;\n\n        &::before {\n            position: absolute;\n            top: 0;\n            right: 0;\n            bottom: 0;\n            left: 0;\n            display: block;\n            z-index: 9;\n            content: \" \";\n            background-color: rgba($white, .85);\n        }\n\n        &::after {\n            position: absolute;\n            top: 50%;\n            left: 50%;\n            margin: -1.5rem 0 0 -1.5rem;\n            width: 3rem;\n            height: 3rem;\n            line-height: 3rem;\n            color: theme-color(\"primary\");\n            font-family: Simple-Line-Icons;\n            font-size: 1.125rem;\n            text-align: center;\n            z-index: 10;\n            content: '\\e09a';\n            animation: fa-spin 1.75s infinite linear;\n            border-radius: 1.5rem;\n        }\n\n        &.block-mode-hidden::after {\n            margin: -1rem 0 0 -1rem;\n            width: 2rem;\n            height: 2rem;\n            line-height: 2rem;\n        }\n\n        &.block-mode-loading-dark {\n            &::before {\n                background-color: rgba($black, .85);\n            }\n\n            &::after {\n                color: $white;\n            }\n        }\n\n        &.block-mode-loading-location::after {\n            content: '\\e06e';\n        }\n\n        &.block-mode-loading-energy::after {\n            content: '\\e020';\n        }\n\n        &.block-mode-loading-refresh::after {\n            font-family: 'Font Awesome 5 Free', 'Font Awesome 5 Pro';\n            font-weight: 900;\n            content: '\\f021';\n        }\n\n        &.block-mode-loading-sun::after {\n            font-family: 'Font Awesome 5 Free', 'Font Awesome 5 Pro';\n            font-weight: 900;\n            content: '\\f185';\n        }\n\n        &.block-mode-loading-repeat::after {\n            font-family: 'Font Awesome 5 Free', 'Font Awesome 5 Pro';\n            font-weight: 900;\n            content: '\\f01e';\n        }\n    }\n\n    &.block-mode-fullscreen {\n        position: fixed;\n        top: 0;\n        right: 0;\n        bottom: 0;\n        left: 0;\n        z-index: $zindex-block-fullscreen;\n        margin-bottom: 0;\n        overflow-y: auto;\n        backface-visibility: hidden;\n        -webkit-overflow-scrolling: touch;\n\n        &.block-bordered {\n            border: none;\n        }\n\n        &.block-rounded {\n            border-radius: 0;\n        }\n\n        &.block-transparent {\n            background-color: $white;\n        }\n    }\n\n    &.block-mode-pinned {\n        position: fixed;\n        right: .75rem;\n        bottom: 0;\n        margin-bottom: 0 !important;\n        width: 100%;\n        max-width: 300px;\n        z-index: $zindex-block-pinned;\n        box-shadow: 0 0 2rem rgba(0,0,0,.1);\n\n        > .block-content {\n            max-height: 250px;\n            overflow-y: auto;\n        }\n    }\n}\n\n// Block Links\na.block {\n    display: block;\n    color: $body-color;\n    font-weight: normal;\n    transition: transform .15s ease-out, opacity .15s ease-out, box-shadow .15s ease-out;\n\n    @include hover() {\n        color: $body-color;\n        opacity: .65;\n    }\n\n    &:active {\n        opacity: 1;\n    }\n\n    &.block-link-rotate {\n        &:hover {\n            transform: rotate(1deg);\n            opacity: 1;\n        }\n\n        &:active {\n            transform: rotate(0deg);\n        }\n    }\n\n    &.block-link-pop {\n        &:hover {\n            box-shadow: 0 .5rem 2rem darken($body-bg, 8%);\n            transform: translateY(-2px);\n            opacity: 1;\n        }\n\n        &:active {\n            box-shadow: 0 .25rem .75rem darken($body-bg, 1%);\n            transform: translateY(0);\n        }\n    }\n\n    &.block-link-shadow {\n        &:hover {\n            box-shadow: 0 0 2.25rem darken($body-bg, 8%);\n            opacity: 1;\n        }\n\n        &:active {\n            box-shadow: 0 0 1.125rem darken($body-bg, 4%);\n        }\n    }\n}\n\n// Block Effects\n.block {\n    &.block-fx-shadow {\n        box-shadow: 0 0 2.25rem darken($body-bg, 8%);\n        opacity: 1;\n    }\n\n    &.block-fx-pop {\n        box-shadow: 0 .5rem 2rem darken($body-bg, 8%);\n        opacity: 1;\n    }\n\n    &.block-fx-rotate-right {\n        transform: rotate(1deg);\n        opacity: 1;\n    }\n\n    &.block-fx-rotate-left {\n        transform: rotate(-1deg);\n        opacity: 1;\n    }\n}\n\n// Block Options\n.block-options {\n    flex: 0 0 auto;\n    padding-left: $space-block;\n\n    .block-options-item {\n        padding: 0 .25rem;\n        line-height: 1.2;\n\n        .block.block-themed & {\n            color: $white;\n        }\n    }\n\n    .block-options-item,\n    .dropdown {\n        display: inline-block;\n    }\n}\n\n.block-sticky-options {\n    position: relative;\n\n    .block-options {\n        position: absolute;\n        top: 14px;\n        right: $space-block;\n\n        &.block-options-left {\n            right: auto;\n            left: $space-block;\n            padding-right: 10px;\n            padding-left: 0;\n        }\n    }\n}\n\n.btn-block-option {\n    display: inline-block;\n    padding: 6px 5px;\n    line-height: 1;\n    color: theme-color(\"primary\");\n    background: none;\n    border: none;\n    cursor: pointer;\n\n    &.dropdown-toggle:after {\n        position: relative;\n        top: 2px;\n    }\n\n    .si {\n        position: relative;\n        top: 1px;\n    }\n\n    @include hover() {\n        text-decoration: none;\n        color: theme-color(\"primary-light\");\n    }\n\n    @at-root {\n        a#{&}:focus,\n        .active > a#{&},\n        .show > button#{&} {\n            text-decoration: none;\n            color: theme-color(\"primary-light\");\n        }\n    }\n\n    &:focus {\n        outline: none !important;\n        color: theme-color(\"primary-light\");\n    }\n\n    &:active {\n        color: theme-color(\"primary-lighter\");\n    }\n\n    .block.block-themed & {\n        color: $white;\n        opacity: .7;\n\n        @include hover() {\n            color: $white;\n            opacity: 1;\n        }\n\n        @at-root {\n            a#{&}:focus,\n            .active > a#{&},\n            .show > button#{&} {\n                color: $white;\n                opacity: 1;\n            }\n        }\n\n        &:focus {\n            color: $white;\n            opacity: 1;\n        }\n\n        &:active {\n            color: $white;\n            opacity: .6;\n        }\n    }\n}\n","//\n// Page Loader\n// --------------------------------------------------\n\n#page-loader {\n    position: fixed;\n    top: 0;\n    right: 0;\n    bottom: 0;\n    left: 0;\n    background-color: theme-color(\"primary\");\n    z-index: 999998;\n    transform: translateY(-100%);\n\n    &.show {\n        transform: translateY(0);\n    }\n\n    &::after,\n    &::before {\n        position: absolute;\n        top: 50%;\n        left: 50%;\n        display: block;\n        border-radius: 100%;\n        content: '';\n        z-index: 999999;\n    }\n\n    &::after {\n        margin-top: -160px;\n        margin-left: -160px;\n        width: 320px;\n        height: 320px;\n        background-color: rgba($white, .5);\n        border: 4px solid rgba($white, .75);\n        box-shadow: 0 0 100px rgba($black, .15);\n        animation: page-loader-inner 1.25s infinite ease-out;\n    }\n\n    &::before {\n        margin-top: -40px;\n        margin-left: -40px;\n        width: 80px;\n        height: 80px;\n        background-color: rgba($white, 1);\n        animation: page-loader-outer 1.25s infinite ease-in;\n    }\n}\n\n\n@keyframes page-loader-inner {\n    0%, 40% {\n        transform: scale(0);\n    }\n\n    100% {\n        transform: scale(1);\n        opacity: 0;\n    }\n}\n\n@keyframes page-loader-outer {\n    0% {\n        transform: scale(1);\n    }\n\n    40%, 100% {\n        transform: scale(0);\n        opacity: 0;\n    }\n}\n","//\n// Main Navigation\n// --------------------------------------------------\n\n.nav-main {\n    @include list-unstyled;\n}\n\n// Headings\n.nav-main-heading {\n    padding-top: 1.75rem;\n    padding-bottom: .25rem;\n    padding-left: .625rem;\n    font-size: .8125rem;\n    font-weight: 500;\n    text-transform: uppercase;\n    letter-spacing: .075rem;\n    color: lighten($body-color, 30%);\n}\n\n// Nav li items\n.nav-main-item {\n    // Fixing flexbox IE10-11 bug, so we can use min-height in links and align their content vertically\n    display: flex;\n    flex-direction: column;\n}\n\n// Default links\n.nav-main-link {\n    position: relative;\n    display: flex;\n    align-items: center;\n    padding: .5rem .625rem;\n    margin: 2px 0;\n    min-height: 2.25rem;\n    font-size: $font-size-sm;\n    font-weight: 500;\n    line-height: 1.25rem;\n    letter-spacing: .0125em;\n    color: lighten($body-color, 5%);\n    border-radius: $border-radius-sm;\n\n    .nav-main-link-icon {\n        flex: 0 0 auto;\n        display: inline-block;\n        margin-right: .625rem;\n        min-width: 1.25rem;\n        font-size: 1rem;\n        text-align: center;\n        color: $main-nav-link-icon-color;\n    }\n\n    .nav-main-link-name {\n        flex: 1 1 auto;\n        display: inline-block;\n        max-width: 100%;\n    }\n\n    .nav-main-link-badge {\n        flex: 0 0 auto;\n        display: inline-block;\n        margin-left: .625rem;\n        padding-right: .375rem;\n        padding-left: .375rem;\n        font-size: .75rem;\n    }\n\n    &:hover,\n    &.active {\n        color: $black;\n        background-color: $main-nav-link-hover-bg;\n    }\n\n    &.nav-main-link-submenu {\n        padding-right: 2rem;\n\n        &::before,\n        &::after {\n            position: absolute;\n            top: 50%;\n            right: .625rem;\n            display: block;\n            margin-top: -.5rem;\n            width: 1rem;\n            height: 1rem;\n            line-height: 1rem;\n            text-align: center;\n            font-family: 'Font Awesome 5 Free', 'Font Awesome 5 Pro';\n            font-weight: 900;\n            transition: opacity $main-nav-transition, transform $main-nav-transition;\n        }\n\n        &::before {\n            content: '\\f107';\n            opacity: .4;\n        }\n\n        &::after {\n            content: '\\f106';\n            opacity: 0;\n            transform: translateY(-.625rem);\n        }\n    }\n}\n\n// Sub menus\n.nav-main-submenu {\n    padding-left: 2.5rem;\n    list-style: none;\n    height: 0;\n    overflow: hidden;\n    background-color: $main-nav-submenu-bg;\n    @include border-bottom-radius($border-radius-sm);\n\n    .nav-main-item {\n        opacity: 0;\n        transition: opacity $main-nav-transition, transform $main-nav-transition;\n        transform: translateX(1rem);\n    }\n\n    .nav-main-heading {\n        padding-top: 1.25rem;\n        padding-bottom: .25rem;\n    }\n\n    .nav-main-link {\n        margin: 0;\n        padding-top: .375rem;\n        padding-bottom: .375rem;\n        padding-left: 0;\n        font-size: .8125rem;\n        min-height: 2rem;\n        color: lighten($body-color, 20%);\n\n        &:hover,\n        &.active {\n            color: darken($body-color, 7.5%);\n            background-color: transparent;\n        }\n    }\n\n    .nav-main-submenu {\n        padding-left: .75rem;\n    }\n}\n\n// Active sub menu\n.nav-main-item.open {\n    > .nav-main-link-submenu {\n        color: $black;\n        background-color: $main-nav-link-hover-bg;\n        @include border-bottom-radius(0);\n\n        &::before {\n            opacity: 0;\n            transform: translateY(.625rem);\n        }\n\n        &::after {\n            opacity: .6;\n            transform: translateY(0);\n        }\n    }\n\n    > .nav-main-submenu {\n        height: auto;\n        margin-top: -2px;\n        padding-top: .375rem;\n        padding-bottom: .375rem;\n\n        > .nav-main-item {\n            opacity: 1;\n            transform: translateX(0);\n        }\n    }\n}\n\n.nav-main-submenu .nav-main-item.open .nav-main-link {\n    background-color: transparent;\n}\n\n// Nav Main Horizontal\n@include media-breakpoint-up(lg) {\n    .nav-main-horizontal {\n        display: flex;\n        flex-wrap: wrap;\n        margin-bottom: 0;\n\n        // Headings\n        .nav-main-heading {\n            display: none;\n        }\n\n        // Nav li items\n        > .nav-main-item {\n            position: relative;\n            display: inline-block;\n\n            &:not(:last-child) {\n                margin-right: .25rem;\n            }\n        }\n\n        // Sub menus\n        .nav-main-submenu {\n            position: absolute;\n            left: 0;\n            width: 220px;\n            padding-left: 0;\n            z-index: $zindex-dropdown - 5;\n            box-shadow: 0 .25rem 2rem rgba(0,0,0,.08);\n\n            &.nav-main-submenu-right {\n                left: auto;\n                right: 0;\n            }\n\n            .nav-main-link {\n                &.nav-main-link-submenu {\n                    &::before {\n                        content: '\\f105';\n                    }\n\n                    &::after {\n                        content: '\\f104';\n                    }\n                }\n            }\n\n            .nav-main-item {\n                transform: translateY(-.5rem);\n            }\n\n            .nav-main-link {\n                padding-left: 1rem;\n            }\n        }\n\n        // Active sub menu\n        .nav-main-item.open {\n            > .nav-main-submenu {\n                padding-top: .5rem;\n                padding-bottom: .5rem;\n                overflow: visible;\n\n                > .nav-main-item {\n                    transform: translateY(0);\n                }\n            }\n        }\n\n        // Sub menus - 2++ Level\n        .nav-main-submenu .nav-main-submenu {\n            top: -.5rem;\n            left: auto;\n            right: -100%;\n            margin-top: 0;\n        }\n\n        // Menu variations\n        &.nav-main-horizontal-center {\n            justify-content: center;\n        }\n\n        &.nav-main-horizontal-justify {\n            > .nav-main-item {\n                flex: 1 1 auto;\n            }\n        }\n\n        &.nav-main-hover {\n            .nav-main-item:hover {\n                > .nav-main-link-submenu {\n                    color: $black;\n                    background-color: $main-nav-link-hover-bg;\n                    @include border-bottom-radius(0);\n\n                    &::before {\n                        opacity: 0;\n                        transform: translateY(.625rem);\n                    }\n\n                    &::after {\n                        opacity: .6;\n                        transform: translateY(0);\n                    }\n                }\n\n                > .nav-main-submenu {\n                    height: auto;\n                    margin-top: -2px;\n                    padding-top: .5rem;\n                    padding-bottom: .5rem;\n                    overflow: visible;\n\n                    .nav-main-submenu {\n                        margin-top: 0;\n                    }\n\n                    > .nav-main-item {\n                        opacity: 1;\n                        transform: translateY(0);\n                    }\n                }\n            }\n        }\n    }\n}\n\n// Dark Variation\n.nav-main-dark,\n.sidebar-dark #sidebar,\n.page-header-dark #page-header {\n    // Headings\n    .nav-main-heading {\n        color: lighten($sidebar-dark-bg, 30%);\n    }\n\n    // Default links\n    .nav-main-link {\n        font-weight: 400;\n        color: lighten($sidebar-dark-bg, 55%);\n\n        > .nav-main-link-icon {\n            color: $main-nav-link-icon-dark-color;\n        }\n\n        &:hover,\n        &.active {\n            color: $white;\n            background-color: $main-nav-link-dark-hover-bg;\n        }\n    }\n\n    // Sub menus\n    .nav-main-submenu {\n        background-color: $main-nav-submenu-dark-bg;\n\n        .nav-main-link {\n            color: lighten($sidebar-dark-bg, 45%);        \n\n            &:hover,\n            &.active {\n                color: $white;\n                background-color: transparent;\n            }\n        }\n    }\n\n    // Active sub menu\n    .nav-main-item.open {\n        > .nav-main-link-submenu {\n            color: $white;\n            background-color: $main-nav-link-dark-hover-bg;\n        }\n\n        > .nav-main-submenu {\n            background-color: $main-nav-submenu-dark-bg;\n        }\n    }\n\n    .nav-main-submenu .nav-main-item.open .nav-main-link {\n        background-color: transparent;\n    }\n}\n\n@include media-breakpoint-up(lg) {\n    .nav-main-dark.nav-main-horizontal.nav-main-hover,\n    .sidebar-dark #sidebar .nav-main-horizontal.nav-main-hover,\n    .page-header-dark #page-header .nav-main-horizontal.nav-main-hover {\n        .nav-main-link {\n            color: rgba($white, .75);\n\n            > .nav-main-link-icon {\n                color: rgba($white, .4);\n            }\n\n            &:hover,\n            &.active {\n                color: $white;\n                background-color: darken($header-dark-bg, 5%);\n            }\n        }\n\n        .nav-main-item:hover {\n            > .nav-main-link-submenu {\n                color: $white;\n                background-color: darken($header-dark-bg, 5%);\n            }\n\n            > .nav-main-submenu {\n                background-color: darken($header-dark-bg, 5%);\n            }\n        }\n\n        .nav-main-submenu .nav-main-item:hover .nav-main-link {\n            background-color: transparent;\n        }\n    }\n}\n","//\n// Image (links and hover options)\n// --------------------------------------------------\n\n// Avatar\n.img-avatar {\n    display: inline-block !important;\n    width: 64px;\n    height: 64px;\n    border-radius: 50%;\n\n    &.img-avatar16 {\n        width: 16px;\n        height: 16px;\n    }\n\n    &.img-avatar20 {\n        width: 20px;\n        height: 20px;\n    }\n\n    &.img-avatar32 {\n        width: 32px;\n        height: 32px;\n    }\n\n    &.img-avatar48 {\n        width: 48px;\n        height: 48px;\n    }\n\n    &.img-avatar96 {\n        width: 96px;\n        height: 96px;\n    }\n\n    &.img-avatar128 {\n        width: 128px;\n        height: 128px;\n    }\n\n    &-thumb {\n        margin: .25rem;\n        box-shadow: 0 0 0 .25rem rgba(255,255,255,.3);\n    }\n\n    &.img-avatar-rounded {\n        border-radius: .25rem;\n    }\n}\n\n// Image Thumb\n.img-thumb {\n    padding: .375rem;\n    background-color: $white;\n    border-radius: .25rem;\n}\n\n// Image Link\n.img-link {\n    display: inline-block;\n    transition: transform .25s ease-out, opacity .25s ease-out;\n\n    @include hover() {\n        transform: scale(1.02);\n        opacity: .75;\n    }\n\n    &:active {\n        transform: scale(1);\n        opacity: 1;\n    }\n\n    &.img-link-simple:hover {\n        transform: scale(1);\n    }\n}\n\n.img-link-zoom-in {\n    cursor: -webkit-zoom-in;\n    cursor: zoom-in;\n}\n\n// Responsive\n.img-fluid.img-fluid-100,\n.img-fluid-100 .img-fluid {\n    width: 100%;\n}\n","//\n// Lists\n// --------------------------------------------------\n\n// Base List\n.list {\n    @include list-unstyled;\n\n    > li {\n        position: relative;\n    }\n\n    &.list-simple > li,\n    &-li-push > li {\n        margin-bottom: 1.25rem;\n    }\n\n    &.list-simple-mini > li,\n    &-li-push-sm > li {\n        margin-bottom: .625rem;\n    }\n\n    &-icons > li {\n        margin-bottom: 1.25rem;\n        padding-left: .5rem;\n    }\n}\n\n// Activity\n.list-activity {\n    > li {\n        margin-bottom: .75rem;\n        padding-bottom: .375rem;\n        padding-left: 2rem;\n        border-bottom: 1px solid $body-bg;\n\n        > i:first-child {\n            position: absolute;\n            left: 0;\n            top: 0;\n            display: inline-block;\n            width: 1.25rem;\n            height: 1.25rem;\n            line-height: 1.25rem;\n            text-align: center;\n        }\n    }\n\n    > li:last-child {\n        border-bottom: none;\n    }\n}\n\n// Events\n.list-events {\n    > li {\n        margin-bottom: .25rem;\n    }\n\n    .js-event {\n        background-color: theme-color(\"info\");\n        transition: transform .3s ease-out;\n\n        @include hover() {\n            cursor: move;\n            transform: translateX(.25rem);\n        }\n    }\n}\n","//\n// Item\n// --------------------------------------------------\n\n.item {\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    width: 4rem;\n    height: 4rem;\n    transition: opacity .25s ease-out, transform .25s ease-out;\n\n    @at-root {\n        a#{&} {\n            will-change: opacity;\n\n            @include hover-focus {\n                opacity: .6;\n            }\n\n            &:active {\n                opacity: 1;\n            }\n\n            &.item-link-pop {\n                will-change: transform;\n\n                @include hover-focus {\n                    opacity: 1;\n                    transform: scale(1.1);\n                }\n\n                &:active {\n                    transform: scale(1);\n                }\n            }\n        }\n    }\n\n    &.item-tiny {\n        width: 1rem;\n        height: 1rem;\n    }\n\n    &.item-2x {\n        width: 6rem;\n        height: 6rem;\n    }\n\n    &.item-3x {\n        width: 8rem;\n        height: 8rem;\n    }\n\n    &.item-circle {\n        border-radius: 50%;\n    }\n\n    &.item-rounded {\n        border-radius: $border-radius;\n    }\n\n    &.item-rounded-big {\n        border-radius: 1.75rem;\n\n        &.item-2x {\n            border-radius: 2.25rem;\n        }\n\n        &.item-3x {\n            border-radius: 3rem;\n        }\n    }\n}\n","//\n// Overlay\n// --------------------------------------------------\n\n// Icon Overlay\n.overlay-container {\n    position: relative;\n}\n\n.overlay-item {\n    position: absolute;\n    display: inline-block;\n    top: 0;\n    right: 0;\n    line-height: 1;\n\n    .overlay-left & {\n        right: auto;\n        left: 0;\n    }\n\n    .overlay-bottom & {\n        top: auto;\n        bottom: 0;\n    }\n\n    .overlay-center & {\n        top: 50%;\n        right: 50%;\n        transform: translateX(50%) translateY(-50%);\n    }\n}\n\n// Options Overlay\n.options-container {\n    position: relative;\n    overflow: hidden;\n    z-index: 1;\n    display: block;\n\n    .options-item {\n        transition: transform .4s ease-out;\n        will-change: transform;\n    }\n\n    .options-overlay {\n        display: flex;\n        justify-content: center;\n        align-items: center;\n        position: absolute;\n        top: -2px;\n        right: -2px;\n        bottom: -2px;\n        left: -2px;\n        z-index: 2;\n        content: '';\n        opacity: 0;\n        visibility: hidden;\n        transition: all .3s ease-in;\n        will-change: opacity, transform;\n\n        &-content {\n            text-align: center;\n        }\n    }\n\n    @include hover() {\n        .options-overlay {\n            opacity: 1;\n            visibility: visible;\n        }\n    }\n\n    @include media-breakpoint-down(sm) {\n        .options-overlay {\n            display: none;\n        }\n\n        &:hover .options-overlay {\n            display: flex;\n        }\n    }\n}\n\n// Item hover effects\n.fx-item-zoom-in:hover .options-item {\n    transform: scale(1.2, 1.2);\n}\n\n.fx-item-rotate-r:hover .options-item {\n    transform: scale(1.4) rotate(8deg);\n}\n\n.fx-item-rotate-l:hover .options-item {\n    transform: scale(1.4) rotate(-8deg);\n}\n\n// Overlay hover effects\n.fx-overlay-slide-top {\n    .options-overlay {\n        transform: translateY(100%);\n    }\n\n    &:hover .options-overlay {\n        transform: translateY(0);\n    }\n}\n\n.fx-overlay-slide-right {\n    .options-overlay {\n        transform: translateX(-100%);\n    }\n\n    &:hover .options-overlay {\n        transform: translateX(0);\n    }\n}\n\n.fx-overlay-slide-down {\n    .options-overlay {\n        transform: translateY(-100%);\n    }\n\n    &:hover .options-overlay {\n        transform: translateY(0);\n    }\n}\n\n.fx-overlay-slide-left {\n    .options-overlay {\n        transform: translateX(100%);\n    }\n\n    &:hover .options-overlay {\n        transform: translateX(0);\n    }\n}\n\n.fx-overlay-zoom-in {\n    .options-overlay {\n        transform: scale(0, 0);\n    }\n\n    &:hover .options-overlay {\n        transform: scale(1, 1);\n    }\n}\n\n.fx-overlay-zoom-out {\n    .options-overlay {\n        transform: scale(2, 2);\n    }\n\n    &:hover .options-overlay {\n        transform: scale(1, 1);\n    }\n}\n","//\n// Timeline\n// --------------------------------------------------\n\n.timeline {\n    position: relative;\n    padding: 1rem 0;\n    @include list-unstyled();\n\n    &::before {\n        position: absolute;\n        top: 0;\n        left: 1.375rem;\n        height: 100%;\n        width: .25rem;\n        background-color: $body-bg-dark;\n        content: \"\";\n        border-radius: $border-radius;\n    }\n}\n\n.timeline-event {\n    position: relative;\n\n    &:not(:last-child) {\n        margin-bottom: $space-base;\n    }\n}\n\n.timeline-event-icon {\n    position: absolute;\n    width: 3rem;\n    height: 3rem;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    color: $white;\n    border-radius: 1.5rem;\n    border: .25rem solid $white;\n    z-index: 5;\n    box-shadow: 0 .375rem 1.5rem darken($body-bg, 8%);\n\n    &::before {\n        position:absolute;\n        top: 50%;\n        right: -.625rem;\n        width: 0;\n        height: 0;\n        border-top: .625rem solid transparent;\n        border-bottom: .625rem solid transparent;\n        border-left: .625rem solid $white;\n        content: \"\";\n        transform: translateY(-50%);\n    }\n}\n\n.timeline-event-block {\n    margin-left: 4rem;\n    margin-bottom: 0 !important;\n}\n\n// Centered Timeline in larger screens\n@include media-breakpoint-up(xl) {\n    .timeline-centered {\n        &::before {\n            left: 50%;\n            margin-left: -.125rem;\n        }\n\n        .timeline-event-icon {\n            left: 50%;\n            margin-left: -1.5rem;\n\n            &::before {\n                right: auto;\n                left: -.625rem;\n                border-right: .625rem solid $white;\n                border-left: none;\n            }\n        }\n\n        .timeline-event-time {\n            position: absolute;\n            display: inline-block;\n            top: 1.5rem;\n            left: 50%;\n            padding: .5rem .5rem .5rem 2rem;\n            width: auto;\n            border-radius: $border-radius;\n            background-color: $white;\n            transform: translateY(-50%);\n        }\n\n        .timeline-event-block {\n            width: 46%;\n            margin-left: 0;\n        }\n\n        .timeline-event:not(:last-child) {\n            margin-bottom: $space-base * 2;\n        }\n\n        &.timeline-alt .timeline-event:nth-child(even),\n        .timeline-event.timeline-event-alt {\n            .timeline-event-icon {\n                left: 50%;\n                margin-left: -1.5rem;\n\n                &::before {\n                    right: -.625rem;\n                    left: auto;\n                    border-right: none;\n                    border-left: .625rem solid $white;\n                }\n            }\n\n            .timeline-event-block {\n                margin-left: auto;\n            }\n\n            .timeline-event-time {\n                padding: .5rem 2rem .5rem .5rem;\n                right: 50%;\n                left: auto;\n                text-align: right;\n            }\n        }\n    }\n}\n","//\n// Ribbon\n// --------------------------------------------------\n\n.ribbon {\n    position: relative;\n    min-height: 3.25rem;\n}\n\n.ribbon-box {\n    position: absolute;\n    top: .75rem;\n    right: 0;\n    padding: 0 .75rem;\n    height: 2rem;\n    line-height: 2rem;\n    z-index: 5;\n    font-weight: $font-weight-bold;\n    border-top-left-radius: $border-radius;\n    border-bottom-left-radius: $border-radius;\n\n    &::before {\n        position: absolute;\n        display: block;\n        width: 0;\n        height: 0;\n        content: '';\n    }\n}\n\n// Bookmark variation\n.ribbon-bookmark {\n    .ribbon-box {\n        padding-left: .625rem;\n        border-radius: 0 !important;\n    }\n\n    .ribbon-box::before {\n        top: 0;\n        right: 100%;\n        height: 2rem;\n        border: 1rem solid;\n        border-left-width: .625rem;\n        border-right-width: 0;\n    }\n}\n\n// Modern variation\n.ribbon-modern {\n    .ribbon-box {\n        top: .75rem;\n        right: -.5rem;\n        padding-left: .75rem;\n        padding-right: .75rem;\n        border-radius: $border-radius !important;\n    }\n\n    .ribbon-box::before {\n        display: none;\n    }\n}\n\n// Position variations\n.ribbon-left {\n    .ribbon-box {\n        right: auto;\n        left: 0;\n        border-radius: 0;\n        border-top-right-radius: $border-radius;\n        border-bottom-right-radius: $border-radius;\n    }\n\n    &.ribbon-bookmark {\n        .ribbon-box {\n            padding-left: .75rem;\n            padding-right: .625rem;\n        }\n\n        .ribbon-box::before {\n            right: auto;\n            left: 100%;\n            border-left-width: 0;\n            border-right-width: .625rem;\n        }\n    }\n\n    &.ribbon-modern {\n        .ribbon-box {\n            left: -.5rem;\n        }\n    }\n}\n\n.ribbon-bottom {\n    .ribbon-box {\n        top: auto;\n        bottom: .75rem;\n    }\n}\n\n// Color variation\n.ribbon-light {\n    @include ribbon-variation($body-bg-dark, $body-color);\n}\n\n.ribbon-dark {\n    @include ribbon-variation(theme-color(\"dark\"), $white);\n}\n\n.ribbon-primary {\n    @include ribbon-variation(theme-color(\"primary\"), $white);\n}\n\n.ribbon-success {\n    @include ribbon-variation(theme-color(\"success\"), $white);\n}\n\n.ribbon-info {\n    @include ribbon-variation(theme-color(\"info\"), $white);\n}\n\n.ribbon-warning {\n    @include ribbon-variation(theme-color(\"warning\"), $white);\n}\n\n.ribbon-danger {\n    @include ribbon-variation(theme-color(\"danger\"), $white);\n}\n\n.ribbon-glass {\n    .ribbon-box {\n        color: $white;\n        background-color: rgba(255,255,255,.4);\n    }\n\n    &.ribbon-bookmark {\n        .ribbon-box::before {\n            border-color: rgba(255,255,255,.4);\n            border-left-color: transparent;\n        }\n\n        &.ribbon-left .ribbon-box::before {\n            border-color: rgba(255,255,255,.4);\n            border-right-color: transparent;\n        }\n    }\n}","//\n// Ribbon\n// --------------------------------------------------\n\n@mixin ribbon-variation($bg-color, $text-color) {\n    .ribbon-box {\n        color: $text-color;\n        background-color: $bg-color;\n    }\n\n    &.ribbon-bookmark {\n        .ribbon-box::before {\n            border-color: $bg-color;\n            border-left-color: transparent;\n        }\n\n        &.ribbon-left .ribbon-box::before {\n            border-color: $bg-color;\n            border-right-color: transparent;\n        }\n    }\n}\n","//\n// Background\n// --------------------------------------------------\n\n// Black/White with opacity\n.bg-black {\n    &-5 { background-color: rgba(0,0,0,.05) !important; }\n    &-10 { background-color: rgba(0,0,0,.1) !important; }\n    &-25 { background-color: rgba(0,0,0,.25) !important; }\n    &-50 { background-color: rgba(0,0,0,.5) !important; }\n    &-75 { background-color: rgba(0,0,0,.75) !important; }\n    &-90 { background-color: rgba(0,0,0,.9) !important; }\n    &-95 { background-color: rgba(0,0,0,.95) !important; }\n}\n\n.bg-white {\n    &-5 { background-color: rgba(255,255,255,.05) !important; }\n    &-10 { background-color: rgba(255,255,255,.1) !important; }\n    &-25 { background-color: rgba(255,255,255,.25) !important; }\n    &-50 { background-color: rgba(255,255,255,.5) !important; }\n    &-75 { background-color: rgba(255,255,255,.75) !important; }\n    &-90 { background-color: rgba(255,255,255,.9) !important; }\n    &-95 { background-color: rgba(255,255,255,.95) !important; }\n}\n\n// Image Background\n.bg-image {\n    background-position: 0% 50%;\n    background-size: cover;\n\n    &-top { background-position-y: 0%; }\n    &-bottom { background-position-y: 100%; }\n\n    @include media-breakpoint-up(xl) {\n        &-fixed { background-attachment: fixed; }\n    }\n}\n\n// Pattern Image\n.bg-pattern {\n    background-repeat: repeat;\n}\n\n// Video Background\n.bg-video {\n    width: 100%;\n    transform: translateZ(0);\n}\n\n// Contextual colors\n@include bg-variant('.bg-primary-op', rgba(theme-color(\"primary\"), .75));\n@include bg-variant('.bg-primary-dark', theme-color(\"primary-dark\"));\n@include bg-variant('.bg-primary-dark-op', rgba(theme-color(\"primary-dark\"), .8));\n@include bg-variant('.bg-primary-darker', t